    border-bottom: 1px solid #ddd;
    border-radius: 4px 4px 0 0;
  }
  .nav-tabs-justified > .active > a,
  .nav-tabs-justified > .active > a:hover,
  .nav-tabs-justified > .active > a:focus {
    border-bottom-color: #fff;
  }
}
.tab-content > .tab-pane {
  display: none;
}
.tab-content > .active {
  display: block;
}
.nav-tabs .dropdown-menu {
  margin-top: -1px;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
}
/* ==================================================================================
   Body
   ================================================================================== */
body {
  margin: 0;
}
/* ==================================================================================
   opened-keditor-sidebar
   ================================================================================== */
body.opened-keditor-sidebar .keditor-content-area {
  margin-right: 260px;
}
body.opened-keditor-sidebar #keditor-sidebar {
  right: 0;
}
/* ==================================================================================
   Mixins
   ================================================================================== */
/* ==================================================================================
   Content area
   ================================================================================== */
.keditor-content-area {
  min-height: 100px;
  width: auto;
  margin: 0;
  padding: 80px 50px 30px;
  max-width: 1200px;
}
/* ==================================================================================
   Sidebar
   ================================================================================== */
#keditor-sidebar {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: -255px;
  bottom: 0;
  background: #f0f0f0;
  opacity: .9;
  border-left: 1px solid #ddd;
  width: 255px;
  box-shadow: -1px 0 5px rgba(0, 0, 0, 0.15);
  padding: 10px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  font: 14px/1.42857143 "Helvetica Neue", Helvetica, Arial, sans-serif;
}
#keditor-sidebar,
#keditor-sidebar *,
#keditor-sidebar:before,
#keditor-sidebar *:before,
#keditor-sidebar:after,
#keditor-sidebar *:after {
  box-sizing: border-box;
}
#keditor-sidebar:hover {
  opacity: 1;
}
#keditor-sidebar-toggler {
  position: absolute;
  top: 100px;
  width: 25px;
  height: 30px;
  left: -25px;
  background: #f0f0f0;
  text-align: center;
  line-height: 30px;
  border: 1px solid #ddd;
  border-right: 0;
  cursor: pointer;
  border-radius: 3px 0 0 3px;
  color: #666;
}
/* ==================================================================================
   Snippets
   ================================================================================== */
#keditor-snippets-list {
  height: 100%;
}
#keditor-snippets-list .keditor-snippet:hover {
  opacity: .6;
  border-color: #ccc;
}
#keditor-snippets-type-switcher {
  margin: 0;
}
#keditor-snippets-container {
  height: calc(100% - 42px);
  background: #fff;
  padding: 10px 5px;
  border: solid #ddd;
  border-width: 0 1px 1px;
}
#keditor-snippets-container > .keditor-snippets {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-transition: opacity 0.3s;
  -o-transition: opacity 0.3s;
  transition: opacity 0.3s;
}
#keditor-snippets-container > .keditor-snippets:before,
#keditor-snippets-container > .keditor-snippets:after {
  content: " ";
  display: table;
}
#keditor-snippets-container > .keditor-snippets:after {
  clear: both;
}
.keditor-snippet {
  padding: 5px;
  border: 1px solid #f0f0f0;
  cursor: move;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  float: left;
  background: #fff;
  margin: 0 5px 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.keditor-snippet img {
  display: block;
}
.keditor-snippet:last-child {
  margin-bottom: 0;
}
/* ==================================================================================
   Toolbar
   ================================================================================== */
.keditor-toolbar {
  position: absolute;
  display: none;
}
.keditor-toolbar,
.keditor-toolbar *,
.keditor-toolbar:before,
.keditor-toolbar *:before,
.keditor-toolbar:after,
.keditor-toolbar *:after {
  box-sizing: border-box;
}
.keditor-toolbar .btn {
  padding: 1px 3px;
  font-size: 14px;
  width: 24px;
  height: 24px;
}
.keditor-toolbar .btn:first-child {
  border-top-right-radius: 0 !important;
}
.keditor-toolbar .btn:last-child {
  border-bottom-right-radius: 0 !important;
}
.keditor-toolbar .btn-container-reposition,
.keditor-toolbar .btn-component-reposition {
  cursor: move;
}
/* ==================================================================================
   Container
   ================================================================================== */
.keditor-container {
  position: relative;
}
.keditor-container > .keditor-toolbar {
  top: -18px;
  left: -39px;
}
.keditor-container > .keditor-toolbar .btn {
  background: #f0f0f0;
  border: 0;
  color: #555;
}
.keditor-container > .keditor-toolbar .btn:hover {
  background: #e0e0e0;
}
.keditor-container.showed-keditor-toolbar {
  outline: 3px dashed #f0f0f0;
  outline-offset: 15px;
  margin: 40px 0;
}
.keditor-container.showed-keditor-toolbar:first-child {
  margin-top: 0;
}
.keditor-container.showed-keditor-toolbar > .keditor-toolbar {
  display: block;
}
.keditor-container-content {
  min-height: 50px;
  outline: 1px dashed #f0f0f0;
  outline-offset: -1px;
}
/* ==================================================================================
   Component
   ================================================================================== */
.keditor-component {
  position: relative;
}
.keditor-component .keditor-toolbar {
  top: 3px;
  right: 3px;
  left: 3px;
  background-color: rgba(0, 0, 0, 0.5);
  padding-bottom: 3px;
  text-align: right;
}
.keditor-component .keditor-toolbar .btn {
  border: 0;
  background: none;
  color: #fafafa;
  width: 20px;
  height: 20px;
  line-height: 20px;
  text-align: center;
  border-radius: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.keditor-component .keditor-toolbar .btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}
.keditor-component.showed-keditor-toolbar {
  outline: 3px solid rgba(0, 0, 0, 0.5);
  outline-offset: -3px;
}
.keditor-component.showed-keditor-toolbar .keditor-component-content {
  outline: none !important;
}
.keditor-component:hover {
  outline: 3px solid rgba(0, 0, 0, 0.5);
  outline-offset: -3px;
}
.keditor-component:hover .keditor-toolbar {
  display: block;
}
/* ==================================================================================
   Drag n Drop helper
   ================================================================================== */
.keditor-snippet,
.keditor-snippet *,
.keditor-snippet:before,
.keditor-snippet *:before,
.keditor-snippet:after,
.keditor-snippet *:after {
  box-sizing: border-box;
}
.keditor-snippet.ui-draggable-dragging {
  -webkit-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}
.ui-sortable-placeholder {
  background: #eee !important;
  visibility: visible !important;
  height: 10px !important;
  margin: 5px 0 !important;
  float: none !important;
}
.keditor-section.ui-sortable-helper {
  background: #fff !important;
  opacity: 0.5;
}
